import React, { useEffect, useState } from "react";
import "../../styles/main/rules.css";
import { CSSTransition } from "react-transition-group";
import { Link } from "react-router-dom";

const Rules = ({ adminMode }) => {
  const [show, setShow] = useState(false);

  useEffect(() => {
    setShow(true);
  }, []);
  return (
    <div className="rules">
      <div className="upper-part">
        <div className="years">
          <div>
            1900
            <br />
            &emsp;-1953
          </div>
          <p> </p>
        </div>
        <div className="title">     </div>
        <div className="start-button">
          <Link to={process.env.PUBLIC_URL + "/game"}>
            <button>
              <img
                src={process.env.PUBLIC_URL + `/images/red-arrow.png`}
                alt="arrow"
              />
            </button>
          </Link>

          <p> </p>
        </div>
      </div>
      <hr className="hr" />
      <div className="description">
        <div className="left">
          <CSSTransition in={show} timeout={300} classNames="alert">
            <h3> &laquo;,    1900 !&raquo;</h3>
          </CSSTransition>
          <p>
                ,   
            &nbsp;  &nbsp;1900 . &nbsp;
               ,  &nbsp;&nbsp;
               &nbsp;&nbsp; 
            &nbsp;     
             XX .
          </p>

          <div className="down">
            <Link
              to={process.env.PUBLIC_URL + "/creators"}
              style={{ textDecoration: "none" }}
            >
              <button className="creators-button"> </button>
            </Link>
            <hr className="small-hr" />
          </div>
        </div>
        <div className="right">
          <h4 className="red">
                  &nbsp; &nbsp;1953 !
          </h4>
          <p>
                &nbsp; . 
                &nbsp; 
             ! !
          </p>
        </div>
      </div>
    </div>
  );
};

export default Rules;
